        </div>
        <div class="mt-font-white">
          {{$numeral(Balance.balance).format('0,0')}}
        </div>
        <div>
          <div>
            授权余额 <span>{{$numeral(Allowance.allowance).format('0,0')}}</span>
          </div>
          <div>
            <div class="van-hairline--surround" @click="handleApprove">
              去授权
            </div>
          </div>
        </div>
        <div class=" mt-font-white8">
          提示：转账前必须要进行授权，如果授权数小于转账数，将无法完成转账。
        </div>
      </div>
    </div>
    <div class="history-area">
        <h4 class="van-hairline--bottom">
          历史记录
        </h4>
        <van-list>
          <div class="history-item van-hairline--bottom" v-for="(history,index) in historys" :key="index">
            <div>
              <div>
                交易号<a :href="`https://etherscan.io/tx/${history.txHash}`" target="_blank"> {{history.txHash.substring(0,10)+'...'+history.txHash.substring(56)}} </a>
              </div>
              <div class="mt-fs-12 mt-font-secondary">{{$moment(history.created_at*1000).format('YYYY-MM-DD HH:mm:ss')}}</div>
