<script>
import { GetSessionObj } from "@/utils/utils";
import { currencyPrices } from "@/service/api";

let historys = [
  {
    txHash : '0x0ba3cc17db1eadd87252fdbf1f80ce16f7cb89e4e9b5016808093cdb00a37c3f',
    created_at: 1536307544
  },
  {
    txHash : '0x0ba3cc17db1eadd87252fdbf1f80ce16f7cb89e4e9b5016808093cdb00a37c3f',
    created_at: 1536307544
  }
]

export default {
  name: "transfer-detail",
  data() {
    return {
      listData: [],
      contractData: {},
      delegate: "0xa0342E0A9FE0b5178208115E7712762c88c228e0",
      Balance: "",
      Allowance: "",
      tokenPrice: 0,
      // 
      historys: historys
    };
  },
  mounted() {
    this.contractData = GetSessionObj("contract");
    currencyPrices({
      contracts: this.contractData.contract
    }).then(res => {
      this.tokenPrice = res.data.data[0].price;
    });
    this.initPossiableFunc();

    window.Ygritte &&
      window.Ygritte.fetchBalance(
        this.contractData.contract,
        "walletNow",
        "MTfetchBalance"
      );

    window.Ygritte &&
      window.Ygritte.fetchBalance(
        this.contractData.contract,
        "walletNow",
        "MTfetchBalance"
      );
    window.Ygritte &&
      window.Ygritte.fetchAllowance(
        this.contractData.contract,
        "walletNow",
        this.delegate,
        "MTfetchAllowance"
      );
  },
  methods: {
    onClickLeft() {
      console.log("top-left-click");
      this.$router.back();
    },
    handleApprove() {
      window.Ygritte.doApprove(
        this.contractData.contract,
        this.delegate,
        "MTdoApprove"
      );
    },
    handleTransaction() {
      console.log(this.Allowance.allowance > 0);
      if (!this.Allowance) {
        this.$toast(`正在获取数据，请稍等!`);
        return;
      }
      if (this.Allowance.allowance === "0") {
        this.$toast(`请先授权使用${this.contractData.symbol}!`);
        return;
      }
      this.$router.push("/dapp/transfer/contract");
    },
    initPossiableFunc() {
      let that = this;
      window.MTfetchBalance = function(res) {
        console.log(res);
        that.Balance = res;
      };
      //  window.fetchBalance = fetchBalance
      window.MTfetchAllowance = function(res) {
        console.log(res);
        that.Allowance = res;
      };
      window.MTdoApprove = function(res) {
        console.log(res);
        that.Allowance = res;
      };
      // window.fetchAllowance = fetchAllowance
    }
  }
};
</script>

